.filter-additional {
  gap: 22px;

  @media (min-width: $tablet-width) {
    gap: 0;
  }
}

.filter-additional__label {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.filter-additional__input {
  min-height: 160px;
  padding: 17px;
  border: 2px solid $text-content;
  font-size: 17px;
  line-height: 18px;
}
